// Google Map styling
::ng-deep .gm-style .gm-style-iw-c {
  padding: 12px !important;
}

// Fix for file input styling
input[type="file"]::file-selector-button {
  margin-right: 0.5rem;
  border: none;
  background: var(--arabic-green);
  padding: 0.5rem 1rem;
  border-radius: 0.375rem;
  color: white;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

input[type="file"]::file-selector-button:hover {
  background: var(--arabic-green-dark);
}

// Make the date picker look better in dark mode
@media (prefers-color-scheme: dark) {
  input[type="date"] {
    color-scheme: dark;
  }
}
